import React from "react";
import { Helmet } from "react-helmet";
import { useState } from "react";
import { NavLink } from 'react-router-dom';


function PersoHome(props)
{
    const [isVerified, setIsVerified] = useState(false);
    if (props.location.search == "")
    {
        global.window && (global.window.location.href = window.location.origin + props.location.pathname + "?password");
        return;
    }

    const checkPw = () =>
    {
        const answer = document.getElementById("password").value;
        if (answer === "test")
        {
            setIsVerified(true);
        }
    };
    return (
        <div>
            <Helmet>
                <title>[P] Home | Lucas RODRIGUEZ</title>
            </Helmet>
            <p><i className="fa fa-home"></i>&nbsp; <NavLink to="/">Lucas RODRIGUEZ's home</NavLink> &bull; <NavLink to="/perso">[P] Home</NavLink></p>

            <div className="p-1 mb-2 bg-danger text-white text-center bg-opacity-80">
                <b>CONFIDENTIAL/PROTECTED</b>
            </div>

            <hr />

            <h2 class="mb-4">Perso - Home</h2>

            <p>Home</p>

            <hr class="col-3 col-md-2 mb-5" />

            {!isVerified ?
                <div className="row">
                    <div className="col-md-4"></div>
                    <div className="col-md-4">
                        <form onSubmit={checkPw}>
                            <div class="form-group mb-3">
                                <input class="form-control" id="password" name="password" type="password" placeholder="Password" autoFocus />
                            </div>
                            {/*<div class="form-group">
                                <input class="form-control" id="password2" name="password2" type="password" placeholder="Password 2" />
                            </div>*/}
                            <button class="btn btn-dark btn-block mt-3" style={{ width: "100%" }}><span class="fa fa-lock"></span>&nbsp;&nbsp;Authenticate</button>
                            {/*<button class="btn btn-danger btn-block mt-2" style={{ width: "100%" }}>SSO/PA</button>*/}
                        </form>
                    </div>
                </div>
                :
                <div>
                    <div class="d-grid gap-2 col-6 mx-auto">
                        <a class="btn btn-dark" href="/perso/efx" role="button">eFX/FX</a>
                        <a class="btn btn-success" href="/perso/trading" role="button">Trading</a>
                    </div>
                </div>}

            <hr class="mt-5" />

            <div className="p-1 mb-2 bg-danger text-white text-center bg-opacity-80">
                <b>CONFIDENTIAL/PROTECTED</b>
            </div>
        </div>
    );
}
export default PersoHome;